import { altitudeTypeOptions, MapSourceType } from '@/common/map-base'

import { useFormRender } from './use-form-render'
import { useModal } from './use-modal'

export default defineComponent({
  name: 'SourceModal',
  emits: ['loadMapSource'],
  setup(props, ctx) {
    const showModalRef = ref(false)
    const formRef = ref()

    const { formModel, specialLabelOptions, ...rest } = useModal()

    const { ...renderRest } = useFormRender({ formModel, specialLabelOptions })

    // 
    const openModal = (key: string) => {
      rest.restFormState()
      formModel.value.dataType = key
      showModalRef.value = true
    }

    // 
    const submitCallback = () => {
      formRef.value?.validate((errors: any) => {
        if (!errors) {
          const type = formModel.value.dataType
          const options = rest.getMapSourceOptions()
          ctx.emit('loadMapSource', { type, options })
          showModalRef.value = false
        } else {
          console.log(errors)
        }
      })
    }

    // 
    const showLonlatFormItem = computed(() => {
      const dataType = formModel.value.dataType
      return (
        dataType === MapSourceType.TILE_SET ||
        dataType === MapSourceType.GLTF_MODEL
      )
    })

    // 
    const showAltitudeTypeFormItem = computed(() => {
      const dataType = formModel.value.dataType
      return (
        dataType === MapSourceType.GLTF_MODEL ||
        dataType === MapSourceType.SPECIAL_SUBJECT
      )
    })

    // 
    const cancelCallback = () => {
      showModalRef.value = false
    }

    ctx.expose({
      openModal
    })

    return {
      showModal: showModalRef,
      formRef,
      formModel,
      ...rest,
      ...renderRest,
      submitCallback,
      cancelCallback,
      altitudeTypeOptions,
      showAltitudeTypeFormItem,
      showLonlatFormItem
    }
  },
  render() {
    const {
      showModal,
      formModel,
      rules,
      renderImageryForm,
      renderTerrainForm,
      renderFeatureForm,
      renderSpecialForm,
      submitCallback,
      cancelCallback
    } = this
    return (
      <NModal
        v-model:show={showModal}
        closable={false}
        showIcon={false}
        preset='card'
        title=''
        bordered={false}
        style={{ width: '600px' }}
        v-slots={{
          footer: () => (
            <NSpace justify='end'>
              <NButton onClick={cancelCallback}></NButton>
              <NButton type='primary' onClick={submitCallback}>
                
              </NButton>
            </NSpace>
          )
        }}
      >
        <NForm
          ref='formRef'
          label-width='auto'
          model={formModel}
          rules={rules}
          size='medium'
        >
          <NGrid x-gap='12'>
            <NGi span='12'>
              <NFormItem label='' path='name'>
                <NInput
                  v-model:value={formModel.name}
                  placeholder=''
                />
              </NFormItem>
            </NGi>
            <NGi span='12'>
              <NFormItem label='' path='age'>
                <NSelect
                  v-model:value={formModel.dataType}
                  disabled
                  options={this.dataTypeOptions}
                />
              </NFormItem>
            </NGi>
            {formModel.dataType === MapSourceType.IMAGE_SERVICE
              ? renderImageryForm()
              : formModel.dataType === MapSourceType.TERRAIN_SERVICE
              ? renderTerrainForm()
              : formModel.dataType === MapSourceType.FEATURE
              ? renderFeatureForm()
              : formModel.dataType === MapSourceType.SPECIAL_SUBJECT
              ? renderSpecialForm()
              : null}
            <NGi span='12'>
              <NFormItem label='' path='url'>
                <NInput
                  v-model:value={formModel.url}
                  placeholder=''
                />
              </NFormItem>
            </NGi>
            <NGi span='12' v-show={this.showAltitudeTypeFormItem}>
              <NFormItem label='' path='age'>
                <NSelect
                  v-model:value={formModel.altitudeType}
                  placeholder='Select'
                  options={this.altitudeTypeOptions}
                />
              </NFormItem>
            </NGi>
            <NGi span='24'>
              <NFormItem
                label=''
                path='url'
                v-show={this.showLonlatFormItem}
              >
                <NSpace align='center' justify='end'>
                  <span></span>
                  <NInputNumber
                    v-model:value={formModel.longitude}
                    placeholder=''
                    style={{ width: '120px' }}
                  />
                  <span></span>
                  <NInputNumber
                    v-model:value={formModel.latitude}
                    placeholder=''
                    style={{ width: '120px' }}
                  />
                  <span></span>
                  <NInputNumber
                    v-model:value={formModel.altitude}
                    placeholder=''
                    style={{ width: '120px' }}
                  />
                </NSpace>
              </NFormItem>
            </NGi>
          </NGrid>
        </NForm>
      </NModal>
    )
  }
})
